.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  padding-top: 5rem;
}
.modalBody {
  padding: 2rem;
  max-width: 400px;
  border-radius: 5px;
  background: #fff;
  max-height: 200px;
}
.openModalBtn{
   margin-bottom: 0.5rem;
}
